import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '../../components/ui/card';
import { Button } from '../../components/ui/button';
import { useQuery } from '@tanstack/react-query';
import {
  Building2,
  FileText,
  Users,
  Shield,
  BarChart3,
  TrendingUp,
  Activity,
  Clock,
  Calendar,
  AlertCircle
} from 'lucide-react';
import { getClinicalSiteStats } from '../../api/services/clinicalSite/clinicalSiteService';
import { getClinicalStudyStats } from '../../api/services/clinicalStudy/clinicalStudyService';
import { getClinicalUserStats } from '../../api/services/clinicalUser/clinicalUserService';
import { getStudyParticipantStats } from '../../api/services/studyParticipant/studyParticipantService';
import { getAllClinicalStudies } from '../../api/services/clinicalStudy/clinicalStudyService';

const ClinicalDashboard = () => {
  // Fetch all statistics using React Query
  const { data: siteStats, isLoading: siteStatsLoading } = useQuery({
    queryKey: ['clinical-site-stats'],
    queryFn: getClinicalSiteStats
  });

  const { data: studyStats, isLoading: studyStatsLoading } = useQuery({
    queryKey: ['clinical-study-stats'],
    queryFn: getClinicalStudyStats
  });

  const { data: userStats, isLoading: userStatsLoading } = useQuery({
    queryKey: ['clinical-user-stats'],
    queryFn: getClinicalUserStats
  });

  const { data: participantStats, isLoading: participantStatsLoading } = useQuery({
    queryKey: ['study-participant-stats'],
    queryFn: getStudyParticipantStats
  });

  const { data: studiesData, isLoading: studiesLoading } = useQuery({
    queryKey: ['clinical-studies'],
    queryFn: getAllClinicalStudies
  });

  const isLoading = siteStatsLoading || studyStatsLoading || userStatsLoading || participantStatsLoading || studiesLoading;

  if (isLoading) {
    return (
      <div className="p-6 space-y-6">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-6"></div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
            {[1, 2, 3, 4].map((i) => (
              <div key={i} className="h-32 bg-gray-200 rounded"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Clinical Management Dashboard</h1>
          <p className="text-gray-600 mt-1">Overview of all clinical research activities</p>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Active Sites</CardTitle>
            <Building2 className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{siteStats?.data?.active_sites || 0}</div>
            <p className="text-xs text-muted-foreground">
              +1 from last month
            </p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Active Studies</CardTitle>
            <FileText className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{studyStats?.data?.active_studies || 0}</div>
            <p className="text-xs text-muted-foreground">
              2 recruiting, 1 active
            </p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Participants</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats?.data?.total || 0}</div>
            <p className="text-xs text-muted-foreground">
              {participantStats?.data?.enrolled || 0} enrolled, {participantStats?.data?.screening || 0} screening, {participantStats?.data?.completed || 0} completed
            </p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Clinical Staff</CardTitle>
            <Shield className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{userStats?.data?.total_clinical_users || 0}</div>
            <p className="text-xs text-muted-foreground">
              {userStats?.data?.principal_investigators || 0} PIs, {userStats?.data?.study_coordinators || 0} coordinators, {userStats?.data?.site_managers || 0} managers
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Main Content Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Activity */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center">
              <Activity className="h-5 w-5 mr-2" />
              Recent Activity
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex items-center space-x-4">
                <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                <div className="flex-1">
                  <p className="text-sm font-medium">Clinical roles system initialized</p>
                  <p className="text-xs text-gray-500">5 default roles created</p>
                </div>
                <div className="text-xs text-gray-400">Just now</div>
              </div>
              
              <div className="flex items-center space-x-4">
                <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                <div className="flex-1">
                  <p className="text-sm font-medium">Sites database ready</p>
                  <p className="text-xs text-gray-500">3 sample sites created</p>
                </div>
                <div className="text-xs text-gray-400">Today</div>
              </div>
              
              <div className="flex items-center space-x-4">
                <div className="w-2 h-2 bg-purple-500 rounded-full"></div>
                <div className="flex-1">
                  <p className="text-sm font-medium">Studies schema deployed</p>
                  <p className="text-xs text-gray-500">Ready for study management</p>
                </div>
                <div className="text-xs text-gray-400">Today</div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Quick Actions */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center">
              <TrendingUp className="h-5 w-5 mr-2" />
              Quick Actions
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 gap-4">
              <button
                onClick={() => window.location.href = '/clinical-roles'}
                className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
              >
                <Shield className="h-8 w-8 text-blue-600 mx-auto mb-2" />
                <p className="text-sm font-medium">Manage Roles</p>
                <p className="text-xs text-gray-500">Configure clinical roles</p>
              </button>

              <button
                onClick={() => window.location.href = '/clinical-users'}
                className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
              >
                <Users className="h-8 w-8 text-green-600 mx-auto mb-2" />
                <p className="text-sm font-medium">Manage Users</p>
                <p className="text-xs text-gray-500">Add clinical staff</p>
              </button>

              <button
                onClick={() => window.location.href = '/clinical-studies'}
                className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
              >
                <FileText className="h-8 w-8 text-purple-600 mx-auto mb-2" />
                <p className="text-sm font-medium">Manage Studies</p>
                <p className="text-xs text-gray-500">Create and manage studies</p>
              </button>

              <button
                onClick={() => window.location.href = '/study-participants'}
                className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
              >
                <Users className="h-8 w-8 text-orange-600 mx-auto mb-2" />
                <p className="text-sm font-medium">Participants</p>
                <p className="text-xs text-gray-500">Enroll and manage participants</p>
              </button>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Study Status Overview */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <BarChart3 className="h-5 w-5 mr-2" />
            Study Status Overview
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {studiesData?.data?.length > 0 ? (
              studiesData.data.slice(0, 3).map((study, index) => {
                const colors = [
                  { bg: 'bg-blue-50', text: 'text-blue-900', subtext: 'text-blue-700' },
                  { bg: 'bg-green-50', text: 'text-green-900', subtext: 'text-green-700' },
                  { bg: 'bg-purple-50', text: 'text-purple-900', subtext: 'text-purple-700' }
                ];
                const color = colors[index % colors.length];

                return (
                  <div key={study.id} className={`flex items-center justify-between p-4 ${color.bg} rounded-lg`}>
                    <div>
                      <h3 className={`font-medium ${color.text}`}>{study.name}</h3>
                      <p className={`text-sm ${color.subtext}`}>
                        {study.phase} • {study.pi_name || 'No PI assigned'}
                      </p>
                    </div>
                    <div className="text-right">
                      <div className={`text-lg font-bold ${color.text}`}>
                        {study.participant_count || 0}/{study.target_enrollment || 0}
                      </div>
                      <div className={`text-sm ${color.subtext}`}>Participants</div>
                    </div>
                  </div>
                );
              })
            ) : (
              <div className="text-center py-8 text-gray-500">
                <FileText className="h-12 w-12 mx-auto mb-4 text-gray-300" />
                <p>No studies available</p>
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* System Status */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <Clock className="h-5 w-5 mr-2" />
            System Status
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="flex items-center space-x-3">
              <div className="w-3 h-3 bg-green-500 rounded-full"></div>
              <div>
                <p className="font-medium">Clinical Roles</p>
                <p className="text-sm text-gray-500">Operational</p>
              </div>
            </div>
            
            <div className="flex items-center space-x-3">
              <div className="w-3 h-3 bg-yellow-500 rounded-full"></div>
              <div>
                <p className="font-medium">Site Management</p>
                <p className="text-sm text-gray-500">In Development</p>
              </div>
            </div>
            
            <div className="flex items-center space-x-3">
              <div className="w-3 h-3 bg-yellow-500 rounded-full"></div>
              <div>
                <p className="font-medium">Study Management</p>
                <p className="text-sm text-gray-500">In Development</p>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ClinicalDashboard;
